<template>
    <div class="user-center">
        <h1>用户中心</h1>
        <div>
            <section>
                <label>账号：</label>
                <span>{{userInfo.account}}</span>
            </section>
            <section>
                <label>身份：</label>
                <span>{{memberInfo}}</span>
            </section>
            <section>
                <ul>
                    <li v-for="(item,index) in vipList" :key="index">
                        <div>{{item.name}}</div>
                        <div>
                            <span>{{item.charge}}元</span>
                            <span>/ 30天</span>
                            <button @click="buy(item)">购买</button>
                        </div>

                    </li>
                </ul>
            </section>
        </div>
    </div>
</template>

<script>
import store from "../store";
import {mapGetters, mapState} from 'vuex'


export default {
    components: {},
    data() {
        return {
            vipList: []
        }
    },
    computed: {
        ...mapState(["userInfo"]),
        ...mapGetters(["memberInfo"])
    },
    mounted() {
        this.vipList = [
            {
                name: "vip会员",
                charge: "10.00",
                vipLevel: 0,
                userStatus: 1
            },
            {
                name: "高级VIP1会员",
                charge: "50.00",
                vipLevel: 1,
                userStatus: 2
            },
            {
                name: "高级VIP3会员",
                charge: "100.00",
                vipLevel: 3,
                userStatus: 2
            },
            {
                name: "高级VIP9会员",
                charge: "1000.00",
                vipLevel: 9,
                userStatus: 2
            },
            {
                name: "高级VIP12会员",
                charge: "10000.00",
                vipLevel: 12,
                userStatus: 2
            }
        ]
    },
    methods: {
        buy(e) {
            store.dispatch('buyVip', e).then(res => {
                alert(res)
            })
        }
    }
}
</script>